<template>
    <div class="content-bg">
      <div class="top-title">日志汇总报表</div>
      <div class="from-wapper">
        <span class="select-lable">日志类型</span>
        <el-select v-model="logType" placeholder="请选择" size="mini" @change="logTypeChange" class="select-list">
          <el-option
            v-for="item in logTypeArr"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span class="select-lable">时间范围</span>
        <el-select v-model="timeVal" placeholder="请选择" size="mini" @change="timeChange" class="select-list">
          <el-option
            v-for="item in timeArr"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="item-wapper">
        <div class="item-title">日志级别数量统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.logLevelData" ></v-echarts>
        </div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.logLevelData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
  import vEcharts from '../common/echarts'
  import {dateFormat} from '../../../static/js/common'
	export default {
		name: "allEquipmentCharts",
		data() {
			return {
        logType:'',
        logTypeArr: [{
          value: '',
          label: '全部'
        }, {
          value: 'syslog',
          label: 'syslog'
        }, {
          value: 'winlog',
          label: 'winlog'
        }, {
          value: 'log4j',
          label: 'log4j'
        }, {
          value: 'mysql',
          label: 'mysql'
        }],
        timeVal:'7',
        timeArr:[{
          value: '7',
          label: '最近7天'
        }, {
          value: '30',
          label: '最近30天'
        }, {
          value: '90',
          label: '最近90天'
        }, {
          value: '',
          label: '全部'
        }],
        //日志级别 柱状图数据
        logLevelData:{
          baseConfig:{
            title:'',
            xAxisName:'日志\n级别',
            yAxisName:'数量',
            rotate:'20',
            itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
          },
          xAxisArr:[],
          yAxisArr:[]

        },
        //日志级别 饼图数据
        logLevelData2:{
          baseConfig:{
            title:'',
            hoverText:'百分比'
          },
          xAxisArr:[],
          yAxisArr:[]

        },
        searchParams:{
          type:'',
          starttime:'',
          endtime:'',
          index:'estest',
          param:'operation_level'
        },
      }
		},
    created(){
		  let currenttime = dateFormat('yyyy-mm-dd',new Date());
		  let starttime = dateFormat('yyyy-mm-dd',new Date(new Date().getTime() - this.timeVal * 24 * 60 * 60 * 1000));
      this.searchParams.starttime = starttime;
      this.searchParams.endtime = currenttime;
      this.searchParams.type = this.logType;
      this.getAllEchartsData(this.searchParams);
    },
    methods:{
      /*获取报表数据*/
      getAllEchartsData(params){
        this.$nextTick(()=>{
          this.$post('/log/getCountGroupByParam.do',params)
            .then((res)=>{
              const obj = res[0];
              const xVal = [];//x轴数据
              const yVal = [];//y轴数据
              const pieVAl = [];//饼图数据
              for(const i in obj){
                const pieObj = {};
                xVal.push(i);
                yVal.push(obj[i])
                pieObj.value = obj[i];
                pieObj.name = i;
                pieVAl.push(pieObj);
              }
              //赋值
              this.logLevelData.xAxisArr = xVal;
              this.logLevelData.yAxisArr = yVal;
              this.logLevelData2.yAxisArr = pieVAl;
            })
            .catch((err)=>{
            })
        })
      },
      /*日志类型改变*/
      logTypeChange(){
        this.searchParams.type = this.logType;
        this.getAllEchartsData(this.searchParams);
      },
      /*日志类型改变*/
      timeChange(){
        let currenttime = '';
        let starttime = '';
        if (this.timeVal !== '') {
          currenttime = dateFormat('yyyy-mm-dd', new Date());
          starttime = dateFormat('yyyy-mm-dd', new Date(new Date().getTime() - this.timeVal * 24 * 60 * 60 * 1000));
        }
        this.searchParams.starttime = starttime;
        this.searchParams.endtime = currenttime;
        this.getAllEchartsData(this.searchParams);
      },
    },
    components:{
      vEcharts
    }
	}
</script>

<style scoped>
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .from-wapper{
    font-size: 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .select-lable{
    box-sizing: border-box;
    font-weight: 100!important;
    /*font-size: 16px;*/
    display: inline-block;
    font-size: 12px;
    height: 28px;
    background: 0;
    border: 1px solid #409eff;
    line-height: 28px;
    padding: 0 5px;
    color: #fff;
    word-break: keep-all;
    color: #409eff;
  }
  .select-list{
    width: 100px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/panel-l-t.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    background: url("../../../static/img/title-bg.png");
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;

  }
  /deep/ .el-input__inner{
    -webkit-border-radius: 0!important;
    -moz-border-radius: 0!important;
    border-radius:0!important;
    position: relative;
    background: 0;
    border: 1px solid #409eff;
    color: #409eff;
  }
</style>



